<template>
    <div>
        <nav-bar>
            <template v-slot:default>新用户注册</template>
        </nav-bar>

            <div style="text-align: center;padding-top: 50px">
                <van-image
                    width="10rem"
                    height="10rem"
                    fit="contain"
                    src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fbpic.588ku.com%2Felement_origin_min_pic%2F17%2F04%2F10%2F8f0d1ecca24308cc22591a44987dd05c.jpg%21%2Ffwfh%2F804x705%2Fquality%2F90%2Funsharp%2Ftrue%2Fcompress%2Ftrue&refer=http%3A%2F%2Fbpic.588ku.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1616743832&t=7e9c2c97ad90230e1c5abbfda2e7ef60"
                />
            </div>

        <van-form @submit="onSubmit">
            <van-field
                v-model="name"
                name="用户名"
                label="用户名"
                placeholder="用户名"
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
                v-model="password"
                type="password"
                name="密码"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <van-field
                v-model="password_confirmation"
                type="password"
                name="确认密码"
                label="确认密码"
                placeholder="确认密码"
                :rules="[{ required: true, message: '请填写一致的密码' }]"
            />
            <van-field
                v-model="email"
                name="电子邮箱"
                label="电子邮箱"
                placeholder="请填写电子邮箱"
                :rules="[{ required: true, message: '请输入正确的电子邮箱格式' }]"
            />
            <div style="margin: 16px;">
                <div class="link-login" @click="$router.push({path:'login'})">
                    已有账号，立即登录
                </div>
                <van-button round block type="info" color="#ff9000" native-type="submit">提交</van-button>
            </div>
        </van-form>

    </div>
</template>

<script>
    import NavBar from 'components/common/navbar/NavBar.vue';
    import {ref, reactive, toRefs} from 'vue';
    import {register} from '../../network/user';
    import { Notify, Toast } from 'vant';
    import {useRouter} from 'vue-router';
    export default {
        name: 'Register',
        components: {
            NavBar,

        },
        setup() {
            const router = useRouter();

            const userinfo = reactive({
                name: '',
                password: '',
                password_confirmation: '',
                email: ''
            })

            const onSubmit = () => {
                // console.log('@@@@@@@@@@@@');
                // 先验证
                if(userinfo.password != userinfo.password_confirmation) {
                    Notify("两次输入密码不一致");
                }else{
                    register(userinfo).then(res => {
                        // console.log(res);
                        if(res.status == '201') {
                            Toast.success('注册成功');

                            setTimeout( () => {
                                router.push({path:'/login'})
                            },1500)
                        }

                        userinfo.password = '';
                        userinfo.password_confirmation = '';

                    })
                }
            }

            return {
                ...toRefs(userinfo),
                onSubmit,
                
            }
        }
    }
</script>

<style lang="scss" scoped>
.link-login {
    font-size: 14px;
    margin-bottom: 10px;
    text-align: center;
    display: inline-block;
    color: #ff5000;
}
</style>